import "./css/style.css";
import React, { useCallback } from "react";
import { useEffect } from "react";
import SealImg from "../../components/SealImg/SealImg";
import { useState } from "react";

const Album = () => {
  const [sealImg, setSealImg] = useState(seals_layouts);
  const onChange = useCallback(
    (e) => {
      let nextSealImg = sealImg.slice(); //재할당

      const url = URL.createObjectURL(e.target.files[0]);
      nextSealImg[e.target.idx] = url;

      setSealImg(nextSealImg);
      console.log(sealImg);
      console.log(nextSealImg);
      console.log(e.target);
    },
    [sealImg]
  );
  return (
    <div className="album-page">
      <div className="album-wrapper">
        <div className="left-bookcover">
          <div className="left-album">
            <div className="left-border">
              {sealImg.map((item, idx) => {
                return <SealImg key={idx} idx={idx} onChange={onChange} />;
              })}
            </div>
          </div>
        </div>
        <div className="right-bookcover">
          <div className="right-album">
            <div className="right-border"></div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Album;
